*::selection {
  background: rgba(230, 28, 47, 0.7);
  color: white; }

input[type=number]::-webkit-inner-spin-button,
input[type=number]::-webkit-outer-spin-button {
  appearance: none;
  margin: 0; }

* {
  box-sizing: border-box; }

*, *:focus {
  outline: 0;
  border: none; }

img {
  display: block; }

a {
  text-decoration: none;
  font-family: sans-serif; }

input[type="button"], input[type="submit"] {
  font-size: 13px;
  border-radius: 3px;
  box-shadow: 1px 5px 5px rgba(0, 0, 0, 0.1), 0px 2px 1px rgba(0, 0, 0, 0.1);
  cursor: pointer; }

p {
  font-size: 13px; }

.main {
  text-align: center;
  min-width: 320px;
  padding: 0; }

.withbackgroundimage {
  background: url("../images/pattern-geo.jpg") center;
  background-size: 70px; }

.login {
  padding: 30px;
  max-width: 425px;
  margin: auto;
  background: white;
  height: 100vh;
  box-shadow: 1px 5px 5px rgba(0, 0, 0, 0.1), 0px 2px 1px rgba(0, 0, 0, 0.1); }
  @media only screen and (min-width: 426px) {
    .login {
      position: absolute;
      width: 425px;
      height: 500px;
      top: 50%;
      left: 50%;
      margin: auto;
      margin-top: -250px;
      margin-left: -214.5px; } }

.logo {
  flex-wrap: wrap;
  height: 60px; }
  .logo__pic, .logo__text {
    width: 250px; }
    .logo__pic img, .logo__text img {
      width: 100%; }
  .logo__text {
    margin-top: 5px;
    width: 150px;
    transform: translateY(20px);
    opacity: 0; }

.loginForm {
  margin-top: 20px;
  display: flex;
  flex-direction: column; }
  .loginForm__headtext {
    text-align: left;
    margin-bottom: 15px; }
  .loginForm__h1, .loginForm__p {
    color: #333333;
    font-weight: normal;
    margin: 0;
    align-self: flex-start; }
  .loginForm__p {
    margin-top: 7px; }
  .loginForm__inputWrapper {
    display: flex;
    flex-wrap: wrap;
    margin-top: 50px;
    transition: all 0.3s ease;
    position: relative;
    height: 34px; }
  .loginForm__input {
    width: 100%;
    padding: 5px 0;
    z-index: 5;
    background: transparent; }
  .loginForm__placeholder {
    position: absolute;
    top: 2px;
    font-family: sans-serif;
    color: #b4b4b4;
    transition: all 0.2s ease-in-out; }
  .loginForm__errormessage {
    color: #e61c2f;
    font-size: 13px;
    margin-top: 5px; }
  .loginForm__underline {
    height: 1px;
    width: 100%;
    border-radius: 2px;
    background: #dddddd;
    position: relative; }
    .loginForm__underline .coloredline {
      position: absolute;
      left: 50%;
      right: 50%;
      height: 2px;
      background: #005f27;
      transition: all 0.2s ease-in-out; }
  .loginForm__button {
    width: 100px;
    background: #005f27;
    color: white;
    height: 35px;
    margin-left: auto;
    font-weight: bold; }
  .loginForm__forgotLink {
    color: #005f27;
    font-size: 14px; }

.dashboard {
  background: pink;
  display: grid;
  width: 300px;
  height: 300px; }

.loginForm__input:focus + .loginForm__underline > .coloredline {
  right: 0;
  left: 0; }

.loginForm__placeholder-isup {
  transform: translateY(-20px);
  font-size: 11px; }

.loginForm__placeholder-error {
  color: #e61c2f; }

.loginForm__underline-error {
  background: #e61c2f;
  height: 2px; }
  .loginForm__underline-error .coloredline {
    background: #e61c2f; }

.logo__text-isup {
  transform: translateY(0);
  opacity: 1; }
